import React from 'react';
import { Form } from 'react-bootstrap';
import validate from "./Functions/validation";
import Pure from './Pure';

const FeildsData = {
    username: {
        value: "",
        isValid: () => false
    },
    email: {
        value: "",
        isValid: () => false
    },
    password: {
        value: "",
        isValid: () => false
    },
    init() {
        this.username.value = "";
        this.email.value = "";
        this.password.value = "";

        this.username.isValid = () => false;
        this.email.isValid = () => false;
        this.password.isValid = () => false;
    },
    setFeild(name = "", fn = () => false) {
        this[name].value = "";
        this[name].isValid = fn;
    },
    set: {
        username(fn = () => false) {
            FeildsData.username.value = "";
            FeildsData.username.isValid = fn;
        },
        password(fn = () => false) {
            FeildsData.password.value = "";
            FeildsData.password.isValid = fn;
        },
        email(fn = () => false) {
            FeildsData.email.value = "";
            FeildsData.email.isValid = fn;
        }
    }
}
class Username extends Pure {
    error = "";
    state = { validated: 0 } // 0=not validated yet, 1=not valid, 2=valid
    onReady() {
        FeildsData.set.username(() => this.check());
    }
    onRemoved() {
        FeildsData.set.username(() => false);
    }
    check() {
        let test = validate.safeUsername(FeildsData.username.value);
        this.error = test.result;
        this.updateState({ validated: test.valid ? 2 : 1 });
        return test.valid;
    }
    render() {
        return <>
            <Form.Control autoFocus="on" tabIndex={this.props.tabIndex || 1} name="name" type="text" size="sm" onBlur={() => this.check()} onChange={e => (FeildsData.username.value = e.target.value)} placeholder="اسم المستخدم" />
            <div className="error ta-right">{this.error}</div>
        </>;
    }
}

